$('.tour-search-form').each((index, component) => {
	let $component = $(component);
	let $countriesSelect = $component.find('.form-input-modal .select');

	$countriesSelect.on('change', (e) => {
		let $trgt = $(e.target);
		let $modal = $trgt.closest('.modal');
		let $changeItem = $component.find(`[data-target="#${$modal.attr('id')}"] .search-form-item__value`);
		let selectValue = $trgt.text();
		$changeItem.text(selectValue);
		$modal.modal('hide');
	});
});
