$('.count-input').each((index, component) => {
	let $component = $(component);
	let $input = $component.find('.count-input__input');
	let $inputText = $input.find('span');
	let $hiddenInput = $component.find('.count-input__input input');
	let $button = $component.find('.count-input__button');
	let count = Number($input.data('min'));
	let inputAttrs = {
		min: Number($input.data('min')) || 0,
		max: Number($input.data('max')) || Infinity,
		placeholder: $input.data('placeholder') || false,
	};

	let inputText = inputAttrs.placeholder;
	$hiddenInput.val(count);

	$button.on('click', (e) => {
		e.preventDefault();
		let $trgt = $(e.target);

		if ($trgt.hasClass('count-input__button_plus')) {
			if (count <inputAttrs.max) {
				count++;

			}
		} else {
			if (count > inputAttrs.min) {
				count--;
			}
		}

		if (inputAttrs.placeholder) {
			if (count > 1) {
				inputText = 'взрослых';
			} else {
				inputText = 'взрослый';
			}

			$inputText.text(`${count} ${inputText}`);
		} else {
			$inputText.text(count);
		}

		$hiddenInput.val(count);
	});
});
