$('.js-form-input-modal-people-count').each((index, component) => {
	let $component = $(component);
	let componentValue;
	let $target = $(`[data-target="#${$component.attr('id')}"] .search-form-item__value`);
	let $submitButton = $component.find('.button-submit');
	let $createInput = $component.find('.create-input');
	let $countInput = $component.find('.count-input');

	function validateCreateInput() {
		let createInputsArr = [];

		$createInput.each((index, item) => {
			createInputsArr[index] = `${$(item).find('.create-input__input').length} ${$(item).data('placeholder').toLowerCase()}`;
		});

		return createInputsArr.concat();
	}

	function validateCountInput() {
		let countInputs = [];

		$countInput.each((index, item) => {
			let itemText = $(item).data('placeholder') || "";
			return countInputs[index] = `${$(item).find('input').val()} ${itemText.toLowerCase()}`;
		});

		return countInputs = countInputs.concat();
	}

	function validateModal() {
		$target.text(`${validateCountInput()} ${validateCreateInput()}`);
	}

	$submitButton.on('click', (e) => {
		e.preventDefault();

		validateModal();
	});
});
