import _ from 'lodash';

$('.create-input').each((index, component) => {
	let $component = $(component);
	let $itemContainer = $component.find('.create-input__container');
	let $dropdownItem = $component.find('.create-input__dropdown-item');
	let $itemTemplate = $component.find('.create-input__input_template');
	let template;

	function createItem() {
		let template = document.createElement('div');
		template.classList.add('create-input__input');
		template.innerHTML = $itemTemplate.html();
		return _.template(template.outerHTML);
	}

	template = createItem();

	$dropdownItem.on('click', (e) => {
		e.preventDefault();

		let $trgt = $(e.target);

		let item = {
			name: $trgt.data('name'),
			value: $trgt.data('value')
		};

		$itemContainer.append(template(item));
	});

	$itemContainer.on('click', '.create-input__close', (e) => {
		e.preventDefault();

		$(e.target).closest('.create-input__input').remove();
	});
});
