$('.search-form-item').each((index, component) => {
	let $component = $(component);
	let $input = $component.find('.search-form-item__input');

	/*$component.on('click', (e) => {
		e.preventDefault();

		$input.focus();
	});*/

	function writeValue(e, value) {
		$input.val(value);
	}

	$component.on('write-value', writeValue);

	//$component.trigger('write-value', 10);
});
