$('.header').each((index, component) => {
	let $component  = $(component);
	let $button = $component.find('.js-button');
	let menuShown = false;

	$(window).on('scroll', () => {
		if ($(window).scrollTop() > 100) {
			$component.addClass('fixed');
		} else {
			$component.removeClass('fixed');
		}
	});

	$(window).on('menu:show', () => {
		$component.stop(true, true).addClass('show-menu');
		$button.stop(true, true).addClass('active');
	});

	$(window).on('menu:hide', () => {
		$component.stop(true, true).removeClass('show-menu');
		$button.stop(true, true).removeClass('active');
	});

	$button.on('click', (e) => {
		e.preventDefault();
		menuShown = !menuShown;

		if (menuShown) {
			$(window).trigger('menu:show');
		} else {
			$(window).trigger('menu:hide');
		}
	});
});
